.call-to-action {
  .title-container {
    width: 50%;
    margin: 0 auto;
    z-index: 10;
    position: relative;
    margin-top: 30px;
    margin-bottom: -100px;
    p {
      font-size: 1.3em;
    }
  }
  .bg-image-container {
    background-size: cover;
    background-position: center center;
    min-height: 400px;
    align-items: center;
    justify-content: center;
    display: flex;
    overflow: hidden;
    .button-container {
      width: 50%;
      z-index: 10;
    }
  }
}
.map-w-marker {
  &.acf-map {
    width: 100%;
    height: 400px;
    img {
       max-width: inherit !important;
    }
  }
}
